<template>
  <div class="swap-box invest-box">
    <HeadTop :title="$t('index.投资记录')" :isShowTop="true" :isSubHeader="false"></HeadTop>
     <van-list v-model:loading="loading" :immediate-check="false" v-model:error="error" :error-text="$t('index.请求失败，点击重新加载')" :finished="finished" :finished-text="$t('index.没有更多了')" @load="onLoad">
    <div class="list-box">
       <div class="item" v-for="(item, index) in list" :key="index">
          <div class="top flex-middle">
            <div class="top-item flex-item">
               <div class="num">{{Number(item.amount/10**18).toFixed(6)}}</div>
               <div class="desc">{{$t('index.推荐收益数量')}}(ELE)</div>
            </div>
          </div>
          <div class="bottom">
             <div class="row flex-middle">
                <div class="name">{{$t('index.结算时间')}}</div>
                <div class="value">{{$dateformat(item.startTime*1000, 'yyyy-mm-dd HH:mm:ss')}}</div>
             </div>
             <div class="row flex-middle">
                <div class="name">{{$t('index.结算地址')}}</div>
                <div class="value">{{item.user.substring(0,8)+ '....' + item.user.substring(item.user.length - 8,item.user.length)}}</div>
             </div>
          </div>
       </div>
    </div>
    </van-list>
  </div>  
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
// import { Toast } from 'vant';
// import config from "@/utils/config";
// import BigNumber from 'bignumber.js';
import { getIntroIncomeList } from "@/api"
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      swapContract: null,
      page: 1,
      limit: 5,
      loading: false,
      error: false,
      finished: false,
      list: [],
      initContract: () => {
        data.initData()
      },
      initData: () => {
         data.list = []
        data.page = 1
        data.getList()
      },
        onLoad () {
        data.page++
        data.getList()
      },
      getList: async () => {
      
          getIntroIncomeList({user:data.address, limit: data.limit, page: data.page}).then((res) => {
             if (data.page === 1) {
            data.list = res.data
          } else {
            data.list.push(...res.data)
          }
          data.loading = false
          data.finished = !(Math.ceil(res.total / data.limit) > data.page)
          })
         
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
